<template>
  <div class="container">
    <!-- 角色 -->
    <figure class="birds" :style="`left:${redBird.left}vw;bottom:${redBird.bottom}vh${redBirdReverse}`">
      <img v-show="redBird.urlSwitch" src="../assets/redBird1.png" />
      <img v-show="!redBird.urlSwitch" src="../assets/redBird2.png" />
    </figure>
    <figure class="birds" :style="`left:${hato.left}vw;bottom:${hato.bottom}vh${hatoReverse}`">
      <img v-show="hato.urlSwitch" src="../assets/hato1.png" />
      <img v-show="!hato.urlSwitch" src="../assets/hato2.png" />
    </figure>
    <!-- 提示文字 -->
    <h2 class="hint">{{ allowDice == true ? 'your turn' : 'progressing' }}</h2>
    <!-- 骰子 -->
    <figure id="dice" @click="turnStart">
      <img v-show="diceNum == 1" src="../assets/dice1.png" />
      <img v-show="diceNum == 2" src="../assets/dice2.png" />
      <img v-show="diceNum == 3" src="../assets/dice3.png" />
      <img v-show="diceNum == 4" src="../assets/dice4.png" />
      <img v-show="diceNum == 5" src="../assets/dice5.png" />
      <img v-show="diceNum == 6" src="../assets/dice6.png" />
    </figure>
    <!-- 賽道 -->
    <!-- 第n層雲格 -->
    <div class="first">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="second">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="third">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="fourth">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="fifth">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      diceNum: 1,
      allowDice: true, // false時禁止擲骰子，避免用戶於回合結束前多次點擊骰子
      hato: {
        urlSwitch: true, // 輪播圖控制切換角色圖片
        position: 0, // 目前所在格數
        left: 4,
        bottom: 8
      },
      redBird: {
        urlSwitch: true,
        position: 0,
        left: 3,
        bottom: 8
      }
    }
  },
  methods: {
    async turnStart() {
      if (this.allowDice) {
        this.allowDice = false // 回合進行中禁止多次投骰
        this.diceChange()
        // 等待投骰動畫
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        // 依照骰出數值移動i次
        for (let i = this.diceNum; i > 0; i--) {
          this.move(this.hato)
        }
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        // 敵人回合
        this.diceChange()
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        for (let i = this.diceNum; i > 0; i--) {
          this.move(this.redBird)
        }
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        // 待回合全結束
        this.allowDice = true
      }
    },
    diceChange() {
      let count = 0 //展示骰子數值變動count次，達成投骰動畫
      let interval = setInterval(() => {
        this.diceNum = Math.floor(Math.random() * 6 + 1)
        count++
        if (count > 10) clearInterval(interval)
      }, 100)
    },
    move(who) {
      // 根據人物當前所在格子決定前進方向
      if (who.position <= 6 || who.position == 8 || who.position == 10) who.left += 9
      if (who.position == 7 || who.position == 9 || who.position == 11 || who.position == 12) who.bottom += 15
      if ((who.position >= 13 && who.position <= 17) || who.position == 19 || who.position == 20 || who.position == 22 || who.position == 23) who.left -= 9
      if (who.position == 18 || who.position == 21 || who.position == 24) who.bottom -= 15
      if (who.position == 25) {
        who.bottom -= 15
        who.position = -1 // 人物前進一格後回到原點，position歸0
      }
      who.position += 1 // 人物前進了一格
    }
  },
  computed: {
    hatoReverse() {
      return this.hato.position >= 13 ? ';transform:scaleX(-1)' : '' // 地圖過半後改變面向左
    },
    redBirdReverse() {
      return this.redBird.position >= 13 ? ';transform:scaleX(-1)' : ''
    }
  },
  mounted() {
    setInterval(() => {
      // 輪播圖切換角色圖片
      this.hato.urlSwitch = !this.hato.urlSwitch
      this.redBird.urlSwitch = !this.redBird.urlSwitch
    }, 500)
  }
}
</script>

<style lang="less" scoped>
img {
  // 所有角色圖片填滿figure標籤
  width: 100%;
  height: 100%;
}
// 遊戲螢幕框
.container {
  position: relative;
  margin: 0 auto;
  padding: 4vh 3vw;
  width: 90vw;
  background: url('../assets/sky.png') no-repeat;
  background-size: cover;
  // 遊戲角色
  .birds {
    position: absolute;
    z-index: 10;
    transition: 1s;
    width: 9vw;
    height: 15vh;
  }
  #dice {
    position: absolute;
    right: 2vw;
    bottom: 5vh;
  }
  // 畫面中央提示框
  .hint {
    position: absolute;
    left: 55%;
    top: 15%;
    background-color: rgba(251, 52, 2, 0.191);
    font: italic 700 2rem Comic Sans MS;
  }
  // 賽道右側雲格
  .floatRight {
    float: right;
  }
  // <figure><img></figure>角色的可控區塊
  figure {
    display: inline-block;
    margin: 0;
    width: 9vw;
    height: 15vh;
  }
  // 第n行雲格
  .first {
    padding: 0 0 0 36vw;
  }
  .second {
    padding: 0 0 0 18vw;
  }
  .fourth {
    padding: 0 9vw 0 0;
  }
  .fifth {
    padding: 0 18vw 0 0;
  }
}
</style>
